<template>
  <div class="row">
    <div class="col-md-12">
      <br />
      <b-card header="Contenu de la carte SD" header-tag="header">
        <div class="bg-default">
          <div class="tree">
            <ul class="mb-1 pl-3 pb-2">
              <tree-item v-for="t in tree" :key="t.key" class="item" :item="t"></tree-item>
            </ul>
          </div>
        </div>
      </b-card>
      <b-card
        header="Upload firmware"
        header-tag="header"
        footer="Mise à jour du GNU Vario"
        footer-tag="footer"
        title="Téléversement"
      >
        <b-form @submit="onSubmitFirmware" @reset="onResetFirmware">
          <b-form-group
            id="input-group-firmware"
            label-cols-sm="12"
            label="Veuillez sélectionner le fichier du firmware à envoyer sur le vario:"
            label-for="input-firmware"
            description="au format .hex"
          >
            <b-form-file id="input-firmware" v-model="file" :state="Boolean(file)" plain></b-form-file>
            <b-progress :value="percentCompleted" :max="100" show-progress animated></b-progress>
          </b-form-group>
          <b-form-group
            id="input-group-path"
            label-cols-sm="12"
            label-for="input-path"
            description="chemin"
          >
            <b-form-input id="input-path" v-model="path" placeholder="Chemin + nom du fichier"></b-form-input>
          </b-form-group>
          <b-button variant="primary" type="submit">Envoyer</b-button>
        </b-form>
      </b-card>
    </div>
  </div>
</template>

<script>
// import axios from "axios";
import TreeItem from "./TreeItem";

export default {
  name: "SD",
  components: { TreeItem },
  props: {
    msg: String
  },
  data: function() {
    return {
      tree: []
    };
  },
  methods: {
    guid() {
      return ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, c =>
        (
          c ^
          (crypto.getRandomValues(new Uint8Array(1))[0] & (15 >> (c / 4)))
        ).toString(16)
      );
    },
    onSubmitFirmware: function() {
      let formData = new FormData();
      formData.append("data", this.file, this.path);
      var self = this;
      var config = {
        headers: {
          "Content-Type": "multipart/form-data"
        },
        onUploadProgress: function(progressEvent) {
          self.percentCompleted = Math.round(
            (progressEvent.loaded * 100) / progressEvent.total
          );
        }
      };

      axios
        .post("/upload", formData, config)
        .then(function() {
          console.log("SUCCESS!!");
        })
        .catch(function() {
          console.log("FAILURE!!");
        });
      return;
    },
    onResetFirmware: function() {
      this.file = null;
      return;
    }
  },
  computed: {
    treeWithKey: function() {
      return this.tree.map(t => {
        return { t, key: this.guid() };
      });
    }
  },
  mounted: function() {
    axios.get("./config/tree.jso").then(response => {
      this.tree = response.data;
    });
  }
};
</script>

<style scoped>
.tree {
  text-align: left;
  margin: 0;
  padding: 0;
  list-style: none;
}
</style>
