<template>
  <li>
    <div :class="{bold: isFolder}" @click="toggle" @dblclick="makeFolder">
      <i
        class="text-secondary fa"
        :class="{ 'fa-folder': (isFolder && !isOpen), 'fa-folder-open': (isFolder && isOpen), 'fa-file': (!isFolder) }"
      ></i>
      &nbsp;
      <a href="javascript:void(0)">{{ item.name }}</a>
    </div>
    <ul v-show="isOpen" v-if="isFolder">
      <tree-item
        class="item"
        v-for="(child, index) in item.contents"
        :key="index"
        :item="child"
        :level="level+1"
        @make-folder="$emit('make-folder', $event)"
        @add-item="$emit('add-item', $event)"
      ></tree-item>
    </ul>
  </li>
</template>

<script>
let nbLevelOpened = 2;
export default {
  name: "TreeItem",
  components: {},
  props: {
    item: Object,
    level: { type: Number, default: 1 }
  },
  data: function() {
    return {
      isOpen: this.level < nbLevelOpened
    };
  },
  methods: {
    toggle: function() {
      if (this.isFolder) {
        this.isOpen = !this.isOpen;
      }
    },
    makeFolder: function() {
      return;
    }
  },
  computed: {
    isFolder: function() {
      return this.item.type == "directory";
    }
  },
  mounted: function() {}
};
</script>

